<template>
	<view class="content">
		<scroll-view scroll-y class="left-aside">
			<view v-for="item in flist" :key="item.id" class="f-item b-b" :class="{active: item.id === currentId}" @click="tabtap(item,1)" @longpress="setOperation(item,1)">
				{{item.name}}
			</view>
			<view class="f-item-add bg-red" @tap="showModal(1,0)">添加一级类目</view>
		</scroll-view>
		<scroll-view scroll-with-animation scroll-y class="right-aside" @scroll="asideScroll" :scroll-top="tabScrollTop">
			<view v-for="item in slist" :key="item.id" class="s-list" :id="'main-'+item.id">
				<text class="s-item" @longpress="setOperation(item,2)">{{item.name}}</text>
				<view class="t-list">
					<view @longpress="setOperation(titem,3)" v-if="titem.pid === item.id" class="t-item text-cut" v-for="titem in tlist" :key="titem.id">
						<image :src="titem.resources.img"></image>
						<text class="text-cut text-center">{{titem.name}}</text>
					</view>
					<view class="t-item text-cut">
						<text class="t-item-add text-cut text-center bg-red padding-xs" @tap="showModal(3,item.id)">添加</text>
					</view>
				</view>
			</view>
			<view class="s-item-add bg-red margin-top" @tap="showModal(2,0)">添加二级类目</view>
		</scroll-view>
		<view class="cu-modal" :class="modalName=='RadioModal'?'show':''">
			<view class="cu-dialog">
				<view class="cu-list menu sm-border">
					<view class="cu-item arrow" @tap="modalName = 'DialogModal1'">
						<view>
							<text>编辑</text>
						</view>
					</view>
					<view class="cu-item arrow" @tap="deleteCategorySubmit()">
						<view >
							<text>删除</text>
						</view>
					</view>
				</view>
			</view>
		</view>
		<view class="cu-modal" :class="modalName=='DialogModal1'?'show':''">
			<view class="cu-dialog">
				<view class="cu-bar bg-white justify-end">
					<view class="action" @tap="hideModal">
						<text class="cuIcon-close text-red"></text>
					</view>
				</view>
				<view class="margin">
					<view class="cu-form-group">
						<view class="title">分类名称</view>
						<input maxlength="30" class="text-right" placeholder="分类名称" v-model="details.name" placeholder-class="placeholder"></input>
					</view>
					<view class="cu-form-group">
						<view class="title">排序</view>
						<input maxlength="30" class="text-right" placeholder="越小越先前" v-model="details.sort" placeholder-class="placeholder"></input>
					</view>
					<view v-if="index === 3" class="cu-form-group">
						<view class="title">分类图标</view>
						<image v-if="details.resources" @click="setLogo" class="cu-avatar radius bg-gray" :src="details.resources.img"/>
						<view v-else class="cu-avatar radius bg-gray" @click="setLogo"></view>
					</view>
				</view>
				
				<view class="cu-bar bg-white justify-end">
					<view class="action">
						<button class="cu-btn line-red text-red" @tap="hideModal">取消</button>
						<button class="cu-btn bg-red margin-left" @tap="setCategory">确定</button>
		
					</view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	import Good from '../../api/good'
	export default {
		data() {
			return {
				sizeCalcState: false,
				tabScrollTop: 0,
				currentId: 0,
				fatherId: 0,
				index: 0,
				flist: [],
				slist: [],
				tlist: [],
				modalName: '',
				details: {
					resources: {
						img: ''
					},
					name: '',
					pid: 0,
					sort: 5
				}
			}
		},
		onLoad(){
			this.loadData();
		},
		methods: {
			async loadData(){
				const that = this
				// 分类
				await Good.getCategoryShow({},function(res){
					res.forEach(item=>{
						if(!item.pid){
							that.flist.push(item);  //pid为父级id, 没有pid或者pid=0是一级分类
						}else if(!item.resources){
							that.slist.push(item); //没有图的是2级分类
						}else{
							that.tlist.push(item); //3级分类
						}
					}) 
				})
			},
			//一级分类点击
			tabtap(item,ind){
				this.currentId = item.id
				
			},
			//操作
			setOperation(item,ind){
				if(!this.sizeCalcState){
					this.calcSize();
				}
				this.fatherId = item.id
				let index = this.slist.findIndex(sitem=>sitem.pid === item.id);
				if(this.slist[index]){
					this.tabScrollTop = this.slist[index].top;
				}
				this.details = item
				this.modalName = 'RadioModal'
				this.index = ind
			},
			
			//右侧栏滚动
			asideScroll(e){
				if(!this.sizeCalcState){
					this.calcSize();
				}
				let scrollTop = e.detail.scrollTop;
				let tabs = this.slist.filter(item=>item.top <= scrollTop).reverse();
				if(tabs.length > 0){
					this.currentId = tabs[0].pid;
				}
			},
			//计算右侧栏每个tab的高度等信息
			calcSize(){
				let h = 0;
				this.slist.forEach(item=>{
					let view = uni.createSelectorQuery().select("#main-" + item.id);
					view.fields({
						size: true
					}, data => {
						item.top = h;
						h += data.height;
						item.bottom = h;
					}).exec();
				})
				this.sizeCalcState = true;
			},
			showModal(e,pid) {
				if(e === 2){
					if(!this.fatherId){
						this.$api.msg('请先选择一级类目')
						return
					}
					this.details.pid = this.currentId
				}else if(e === 3){
					this.details.pid = pid
				}
				this.modalName = 'DialogModal1'
				this.index = e
				
			},
			hideModal(e) {
				this.modalName = null
				this.details = {
					resources: {
						img: ''
					},
					name: '',
					pid: 0,
					sort: 5
				}
			},
			setLogo(){
				const that = this
				uni.chooseImage({
				    count: 1,
				    sizeType: ['compressed'],
				    sourceType: ['album','camera'],
				    success: function (res) {
						uni.uploadFile({
						  url: uni.getStorageSync('applyGoodSecret').host + 'uploadPictures',
						  filePath: res.tempFilePaths[0],
						  name: 'file',
						  header: {
							'apply-secret': uni.getStorageSync('applyGoodSecret').secret,
						  },
						  formData: {
							type: 1,
							size: 1024 * 500
						  },
						  success(res) {
							that.details.resources.img = res.data
						  },
						  fail(res) {
							that.$api.msg(res.message);
							return false
						  }
						})
				    }
				});
			},
			setCategory(){
				let details = this.details
				const that = this
				if(!details.name){
					that.$api.msg('分类名称不能为空');
					return;
				}
				if(!details.sort){
					that.$api.msg('分类排序不能为空');
					return;
				}
				if(this.index === 3){
					if(!details.resources.img){
						that.$api.msg('分类图片不能为空');
						return;
					}
				}
				if(details.id){
					Good.updateCategorySubmit(details,function(res){
						that.$api.msg(`更新成功`)
						that.hideModal()
						that.loadData()
						that.flist = []
						that.slist = []
						that.tlist = []
					})
				}else{
					Good.createCategorySubmit(details,function(res){
						that.$api.msg(`添加成功`)
						that.hideModal()
						that.loadData()
						that.flist = []
						that.slist = []
						that.tlist = []
					})
				}
				
			},
			deleteCategorySubmit(){
				let details = this.details
				const that = this
				Good.deleteCategorySubmit(details.id,function(res){
					that.$api.msg(`删除成功`)
					that.hideModal()
					that.loadData()
					that.flist = []
					that.slist = []
					that.tlist = []
				})
			}
		}
	}
</script>

<style lang='scss'>
	page,
	.content {
		height: 100%;
		background-color: #f8f8f8;
	}

	.content {
		display: flex;
	}
	.left-aside {
		flex-shrink: 0;
		width: 200upx;
		height: 100%;
		background-color: #fff;
	}
	.f-item {
		display: flex;
		align-items: center;
		justify-content: center;
		width: 100%;
		height: 100upx;
		font-size: 28upx;
		color: $font-color-base;
		position: relative;
		&.active{
			color: $base-color;
			background: #f8f8f8;
			&:before{
				content: '';
				position: absolute;
				left: 0;
				top: 50%;
				transform: translateY(-50%);
				height: 36upx;
				width: 8upx;
				background-color: $base-color;
				border-radius: 0 4px 4px 0;
				opacity: .8;
			}
		}
	}

	.right-aside{
		flex: 1;
		overflow: hidden;
		padding-left: 20upx;
	}
	.s-item{
		display: flex;
		align-items: center;
		height: 70upx;
		padding-top: 8upx;
		font-size: 28upx;
		color: $font-color-dark;
	}
	.t-list{
		display: flex;
		flex-wrap: wrap;
		width: 100%;
		background: #fff;
		padding-top: 12upx;
		&:after{
			content: '';
			flex: 99;
			height: 0;
		}
	}
	.t-item{
		flex-shrink: 0;
		display: flex;
		justify-content: center;
		align-items: center;
		flex-direction: column;
		width: 176upx;
		font-size: 26upx;
		color: #666;
		padding-bottom: 20upx;
		
		image{
			width: 140upx;
			height: 140upx;
		}
		.text-cut{
			width: 176upx;
		}
	}
	.f-item-add{
		text-align: center;
		line-height: 80upx;
	}
	.s-item-add{
		text-align: center;
		line-height: 80upx;
	}
	.t-item-add{
		color: #FFFFFF;
		line-height: 80upx;
	}
	.cu-modal{
		z-index: 3;
	}
</style>
